<template>
    <div>
        <div v-if="isbuy || isshare">
            <img :src="imgurl" alt="" class="img">
            <div class="bottom" >
                <div class="kecheng_tips" >{{ tips }}</div>
                <div class="lingqu" @click="showTips()" ></div>
                <div class="goumai" @click="buy()" ></div>      
            </div>
        </div>
        <div v-else>
            <video-player  class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions"
            >
            </video-player>
        </div>
    </div>
</template>

<script>
export default {
    name:'detail',
    data () {
        return {  
            isbuy: '',  
            isshare: '',  
            imgurl: '', 
            tips: '',
            playerOptions : {
                playbackRates: [0.7, 1.0, 1.5, 2.0], //
                autoplay: false, //true,
                muted: false, // 
                loop: false, // 
                preload: 'auto', // <video>auto,
                language: 'zh-CN',
                aspectRatio: '16:9', //  - "16:9""4:3"
                fluid: true, // trueVideo.js player
                sources: [{
                type: "",
                src: "" //url
                }],
                poster: "../../assets/img/share.jpg", //
                // width: document.documentElement.clientWidth,
                notSupportedMessage: '', //Video.js
                controlBar: {
                    timeDivider: true,
                    durationDisplay: true,
                    remainingTimeDisplay: false,
                    fullscreenToggle: true  //
                }
            }
        }  
    },
    created () {
        let buy = this.$route.params.isbuy;
        let share = this.$route.params.isshare;
        let imgurl = this.$route.params.img;
        let tips = this.$route.params.tips;
        let vediourl = this.$route.params.video_url;
        //   
        this.isbuy = buy;
        this.isshare = share;
        this.imgurl = imgurl;
        this.tips = tips;
        this.playerOptions.sources[0].src = vediourl;
    }
}
</script>

<style lang="less" scoped>
    .img{
        width: 100%;
        margin-bottom:100px
    }
    .bottom{
		background-color: white;
		width: 100%;
		position: fixed;
		bottom: 0;			
    }
    .kecheng_tips{
        float: left;
		color: #e06522;
		padding-left: 2.6vw;
		font-size: 4.8vw;
                
        height:15vw;
        line-height:15vw
    }
	.lingqu{
		background-color: #2d79df;
	}
	.goumai{
		background-color: #5b9df7;
    }
    .goumai,.lingqu{
		float: right;
		text-align: center;
		color: white;
		padding-left: 4.2vw;
        padding-right:4.2vw;
		font-size: 4.2vw;
        font-weight:300;
        height:15vw;
        line-height:15vw;		
    }
    .goumai:active
     {
        background-color:#666666;
        cursor:pointer;
     }
     .lingqu:active
     {
        background-color:#666666;
        cursor:pointer;
     }
</style>


